//// TextBlock Component
function TextBlock(props : any) {
  const Other = (props : any) => {
    switch (props.other) {
      case "music":
        return <MusicList />;
      case "illust":
        return <IllustList />;
      case "citing":
        return <CitingList />;
      default:
        return <></>;
    }
  };
  return (
    <div className="textBlock">
      <h2 className="capt">{props.capt}</h2>
      <p className="pr">{props.pr}</p>
      <Other other={props.other} />
    </div>
  );
}

function Footer(props : any) {
  return <p>footer</p>;
}
export default Content;
